		width: 100%;
		height: 100%;
		border: none;
		border-radius: .1rem;
	}
</style>

<script type="text/javascript">
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
export default {
	name: 'feedBack',
	props: {
		feedBack_flag: Boolean,
	},
	data() {
		return {
			feedBack_content: ''
		}
	},
	created() {
		
	},
	computed: {

	},
	methods: {
		showFeedBack: function() {
			bus.$emit('changeFeedBack',!this.feedBack_flag)
			this.feedBack_content= ''
		},
		// 上传反馈内容
		upload_feedback: function() {
			const me =this;
			if(me.feedBack_content != ''){
				me.axios.post("/Feedback/add_feedback",{
					content: me.feedBack_content
				})
				.then(function(data) {
					console.log(data)
					me.$message('感谢你的反馈！敬请期待');
					me.showFeedBack()
				})
				.catch(function(e) {
					console.log(e)
				})
			}else{
				me.$message('反馈内容不能为空')
			}
		}
	}
}

</script>