import Vue from 'vue'
import axios from 'axios'

/**
 * 200成功
	207抠图已达今日上线
	208非法用户（用户id不存在）
	206抠图失败
	204返回路径为空
	10003窗建文件夹失败
 */

export function cutoutByJpg(path, type = 1) { 
	// 创建axios实例
	let instance = axios.create({timeout: 5000});

    return new Promise((resolve, reject) => {         
        instance.post('http://ht.idealead.hbindex.com/webapi/User_cutout/upload_file_once', {
        	path : path,
            type: type,
        	user_id: Vue.prototype.getCookie('author_id')
        })        
        .then(res => { 
        	// console.log(res.data)
        	resolve(res.data);
        	
        })        
        .catch(err => {            
            reject(err)        
        })    
    });
}